<template>
  <div class="mailBody">
    <div class="user-header">
      <a class="nav-item1" @click="mailBack"><img class="game_1" src="../../assets/images/usercenter/game_1.png"/></a>
      <a class="mail">邮寄</a>
    </div>
    <div class="mailMain">
      <div class="mailOrder" v-show="mailShow" v-for="item in orderList">
        <div class="orderHead">
          <a>订单号:{{item.order}}</a>
          <a>申请时间:{{item.created_at}}</a>
        </div>
        <div class="orderBody" v-for="val in item.props">
          <img :src="val.icon_url"/>
          <span>
              <a>{{val.name}}</a>
              <a>×{{val.num}}</a>
          </span>
        </div>
        <div class="orderFoot">
          <a>共{{totalNum}}件</a>
          <div>
            <a v-if="isMail">已发货!</a>
            <a v-else>待发货!</a>
          </div>
          <div>
            <a @click="orderDetails(item)"><img src="../../assets/images/thebar/wh_45.png"/>订单详情</a>
          </div>
        </div>
      </div>
      <div class="nomail" v-show="!mailShow">
        <a>~\(*>﹏<*)/~暂无邮寄订单!</a>
        <img src="../../assets/images/usercenter/wh_49none.png"/>
      </div>
    </div>
  </div>
</template>
<script>
  var urlhost = process.env.API_AJAX;
  export default {
    data() {
      return {
        mailShow:true,     //判断是否有订单
        orderList:null,    //订单列表
        isMail:false,      //判断是否邮寄 
        totalNum:0,        //每个订单娃娃个数
        isFree:false,      //是否包邮
      }
    },
    components: {
    },
    mounted:function () {
      var _this = this;
      var user_id = getCookie('userId');
      $.ajax({
        type: 'GET',
        url: urlhost+"usercenter/packorderlist?user_id=121",
        data$Type: "jsonp",
        data: {
        },
        success: function (response) {
          var mess = JSON.parse(response);
          console.log(mess)
          if(mess.status == 1){
            if(mess.message==''){
              _this.mailShow = false;
            }else{
              _this.mailShow = true;
              var format = function (timestamp) {
                var date = new Date(timestamp * 1000);//时间戳为10位需*1000，时间戳为13位的话不需乘1000
                var Y = date.getFullYear() + '/';
                var M = (date.getMonth()+1 < 10 ? '0'+(date.getMonth()+1) : date.getMonth()+1) + '/';
                var D = date.getDate() + ' ';
                var h = date.getHours() + ':';
                var m = date.getMinutes() < 10 ? '0'+(date.getMinutes()) : date.getMinutes();
                return Y+M+D+h+m;
              }
              var myorder = mess.message;
              for(var i=0;i< myorder.length;i++){
                //时间戳
                var timeNum = parseInt(myorder[i].created_at)
                var time = format(timeNum);
                myorder[i].created_at = time;
                
                //娃娃总数
                for(var j=0;j<myorder[i].props.length;j++){
                  _this.totalNum += myorder[i].props[j].num;
                }

                //判断发货状态
                if(myorder[i].status == 1 || myorder[i].status == 4){
                  _this.isMail = false;
                  if(_this.totalNum >=3){
                    _this.isFree = true;
                  }
                }else{
                  _this.isMail = true;
                }
              }
              _this.orderList = mess.message;
            }
          }
        },
      });

    },
    methods:{
      mailBack:function () {
        this.$router.go(-1);
      },
      orderDetails:function (data) {
        this.$router.push({
          path:"/usercenter/orderDetail",
          query:{
            id:data.id,
            totalNum : this.totalNum
          }
        })
      }
    }
  }
</script>
<style lang="scss" scoped>
  @function n($n) {
    @return $n  / 64 + rem;
  };
  .mailBody{
    position: fixed;
    width: 100%;
    height: 100%;
    overflow-y: auto;
    top: 0px;
    background-color: #f2f2f2;
    .user-header{
      position: fixed;
      top: 0;
      left: 0;
      width: 100%;
      height: n(80);
      background: #fff001;
      display: flex;
      line-height: n(80);
      z-index: 999;
      border-bottom: 1px solid #d8d8d8;
      .nav-item1{
        position: absolute;
        left: 0.2rem;
        .game_1{
          width: n(50);
          height: n(50);
        }
      }
      .mail{
        font-size: 14px;
        font-weight: bold;
        width: 100%;
        display: inline-block;
        text-align: center;
      }
    }
    .mailMain{
      margin-top: n(95);
      width: 95%;
      margin-left: 2.5%;
      .mailOrder{
        margin-top: n(10);
        background: #ffffff;
        .orderHead{
          width: 100%;
          padding: n(15);
          display: inline-flex;
          justify-content: space-between;
          align-items: center;
          height: n(60);
          border-bottom: 1px solid #f2f2f2;

        }
        .orderBody{
          height: n(120);
          border-bottom: 1px solid #f2f2f2;
          display: inline-flex;
          align-items: center;
          width: 95%;
          margin: 0 n(15);
          img{
            float: left;
            display: block;
            width: n(85);
            height: n(85);
            -webkit-border-radius: 5px;
            -moz-border-radius: 5px;
            border-radius: 5px;

          }
          span{
            margin-left: n(15);
            float: left;
            height: n(100);
            display: inline-flex;
            justify-content: center;
            flex-direction: column;
          }
        }
        .orderFoot{
          width: 95%;
          height: n(60);
          display: inline-flex;
          align-items: center;
          justify-content: space-between;
          margin: 0 n(15);
          div{
            img{
              width: n(40);
              height: n(40);
            }
          }
        }
      }
      .nomail{
        width: 100%;
        height: n(400);
        display: inline-flex;
        align-items: center;
        flex-direction: column;
        justify-content: center;
        img{
          margin-top: 10px;
          width: n(349);
          height: n(234);
        }
      }

    }
  }
</style>




